.main{
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__img{
    margin-top: 1.875em;
    img{
      width: calc(100% + 10em);
    }
  }
  &__subtitle{
    color: #359D96;
  }
  &__left{
    padding-top: 5.875em;
    display: flex;
    flex-direction: column;
  }
  &__title{
    text-transform: uppercase;
    @media screen and (min-width: 993px){
      white-space: nowrap;
    }
  }
  &__text2{
    margin-top: .9375em;
    display: flex;
    align-items: flex-end;
  }
  &__hint{
    padding-top: .375em;
    border-top: .0625em solid #272727;
    transform: translateY(1.875em);
    @media screen and (min-width: 993px){
      white-space: nowrap;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 1em;
    display: flex;
  }
  &__btn{
    width: 15em;
    height: 20.875em;
    padding: 1em 1em 1em 1.375em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: left;
    text-transform: uppercase;
    position: relative;
    overflow: hidden;
    &-bg{
      bottom: 3.5em;
      right: -.875em;
      position: absolute;
      height: 10em;
    }
    &-icon{
      width: 1.875em;
      margin-left: auto;
    }
  }
}

.advantages{
  padding: 1.375em 0;
  margin-top: 1.375em;
  &__grid{
    margin-top: 2.75em;
    display: grid;
    grid-template-columns: repeat(4,25%);
  }
}

.advantage-item{
  border: 1px solid $primary;
  display: flex;
  flex-direction: column;
  padding: 1.225em 3.125em 2.5em;
  min-height: 26.25em;
  overflow: hidden;
  .info-block{
    margin-top: auto;
    padding-top: 1em;
    .bold-text{
      color: $black;
    }
  }
  .advantage-days__text,
  .advantage-inspector__hint,
  .advantage-inspector__text,
  .advantage-insurance__hint,
  .advantage-insurance__percent,
  .advantage-office__num,
  .advantage-office__places,
  .advantage-item__hour,
  .info-block{
    transition: all .5s;
    opacity: 0;
    transform: translateY(50px);
  }
  &._animated{
    .advantage-days__text,
    .advantage-inspector__hint,
    .advantage-inspector__text,
    .advantage-insurance__hint,
    .advantage-insurance__percent,
    .advantage-office__num,
    .advantage-office__places,
    .advantage-item__hour,
    .info-block{
      opacity: 1;
      transform: translateY(0);
    }
  }
}

.advantage-hour,
.advantage-inspector{
  grid-column: span 2;
  padding-top: .25em;
}

.advantage-office{
  padding-bottom: .625em;
  margin-left: -1px;
  &__places{
    font-weight: 800;
    max-width: 10em;
    text-transform: uppercase;
  }
  &__num{
    margin-top: auto;
    padding-top: 1em;
    display: flex;
    align-items: flex-end;
    color: $primary;
    &-title{
      margin-left: .875em;
      padding-bottom: 1.5em;
    }
  }
}

.advantage-insurance{
  margin-left: -1px;
  padding-top: .625em;
  border: none;
  background-color: $secondary;
  color: #fff;
  &__hint{
    padding-top: 1em;
    margin-top: auto;
  }
  &__percent{
    &-title{
      margin-top: -1.875em;
    }
  }
}

.advantage-overpayment{
  border: 1px solid $primary;
  background-color: transparent;
  color: $black;
  margin-top: -1px;
  .advantage-insurance__percent{
    color: $primary;
  }
}

.advantage-inspector{
  margin-top: -1px;
  background-color: $primary;
  color: #fff;
  &__text{
    display: flex;
    align-items: flex-end;
    &-title{
      padding-bottom: 2.75em;
    }
  }
  &__hint{
    padding-top: 1em;
    margin-top: auto;
  }
}

.advantage-days{
  margin: -1px 0 0 -1px;
  &__text{
    display: flex;
    align-items: flex-end;
    &-title{
      padding: 0 0 1.5625em 1em;
    }
  }
}

.info-block{
  color: #40516B;
  text-transform: uppercase;
  p{
    font-size: 1.125em;
    line-height: 122%;
  }
}

.consult{
  padding: 3.5em 0 0;
  &__inner{
    display: grid;
    grid-template-columns: 50% 50%;
  }
}

.consult-info{
  max-width: 49.375em;
  background-color: $primary;
  color: #fff;
  padding: 3em 3em 2.75em;
  &__top{
    max-width: 31.25em;
    @include grid100gap(.5em);
  }
  &__bottom{
    margin-top: 7.75em;
    max-width: 35em;
  }
}

.consult-form{
  padding: 2.1875em 0 0 3.5em;
  &__title{
    color: $dark;
  }
  &__fields{
    margin-top: 1.75em;
    @include grid100gap(.75em);
    max-width: 26.625em;
  }
  &__agree{
    margin-top: .3125em;
    color: #9CA4B0;
    button{
      color: inherit;
      text-decoration: underline;
      &:hover{
        text-decoration: none;
        color: $secondary;
      }
    }
  }
  &__btn{
    display: flex;
    justify-content: space-between;
    width: 36.6875em;
    height: 13em;
    margin: 1.875em 0 0 auto;
    padding: 1.5em 2.25em;
    transform: translateX(10em);
    span{
      font-weight: 700;
      font-size: 1.125em;
      line-height: 121%;
      text-decoration-line: underline;
      text-transform: uppercase;
    }
    img{
      right: 0;
      top: 0;
      height: 100%;
      position: absolute;
    }
  }
}

.services{
  padding: 1.75em 0;
  &__top{
    display: flex;
  }
  &__title{
    margin-top: -.75em;
    margin-left: 4em;
    display: flex;
    flex-direction: column;
    @media screen and (min-width: 993px){
      min-width: 35.625em;
      span{
        font-size: 2.875em;
        line-height: 124%;
      }
      .primary-text{
        margin-left: auto;
      }
    }
    span{

    }
  }
  &__grid{
    margin-top: 2.25em;
    display: grid;
    grid-template-columns: repeat(4,25%);
  }
}

.service-card,
.delivery-card{
  &__btn{
    position: absolute;
    left: 0;
    bottom: 0;
    height: 7.4375em;
    width: 100%;
    padding: 0 1.625em 0 3.1875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-transform: uppercase;
    font-weight: 700;
    img{
      width: 1.875em;
    }
  }
}

.service-card{
  position: relative;
  color: #fff;
  &:hover{
    .service-card__btn{
      opacity: 1;
    }
  }
  &__img{
    @include imgCover(225.75%);
  }
  &__content{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: calc(100% - 11em);
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.8) 100%);
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 7.1875em 3.125em;
  }
  &__icon{
    height: 3.75em;
    display: flex;
    align-items: flex-end;
    img{
      max-height: 100%;
      max-width: 9em;
    }
  }
  &__title{
    margin-top: 2em;
    padding-bottom: .75em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      height: 1px;
      width: 6.25em;
      left: 0;
      bottom: 0;
      background-color: #fff;
    }
    span{
      font-weight: 700;
      font-size: 2.25em;
      line-height: 122%;
      text-transform: uppercase;
    }
  }
  &__info{
    margin-top: 1.25em;
  }
  &__btn{
    bottom: -1.865em;
    opacity: 0;
  }
}

.prices{
  padding: 3.5em 0;
  position: relative;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 40em) 40em;
  }
  &__img{
    img{
      display: block;
      width: 100%;
    }
  }
  &__left{
    padding-top: 3em;
    position: relative;
    display: flex;
    flex-direction: column;
  }
  &__top{
    max-width: 46.125em;
  }
  .services__title{
    margin: 0;
    .primary-text{
      margin-top: -.25em;
    }
  }
  &__text{
    margin-top: 3.75em;
    max-width: 31.25em;
  }
  &__img2{
    position: absolute;
    bottom: 12.25em;
    left: 0;
    width: 17.25em;
  }
  &__bottom{
    margin-top: auto;
    padding: 2em 0 2.5em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__text2{
    max-width: 23.75em;
    padding-bottom: 3.3125em;
  }
  &__btn{
    padding: 3.125em;
    width: 25em;
    flex-shrink: 0;
    height: 19.5em;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    position: relative;
    font-weight: 700;
    img{
      position: absolute;
      right: 0;
      bottom: 0;
      width: 15em;
    }
  }
}

.delivery{
  padding: .375em 0 3.125em;
  &__top{
    max-width: 40em;
    margin-left: auto;
    margin-right: 13.75em;
    display: flex;
    align-items: flex-end;
  }
  &__title{
    padding: 0 0 3em;
    .secondary-text{
      margin-top: -.17em;
    }
  }
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.delivery-card{
  display: flex;
  flex-direction: column;
  position: relative;
  &._blue{
    .delivery-card__top{
      background-color: $primary;
    }
    .delivery-card__list li:before{
      background-image: url(../img/check2.svg);
    }
    .delivery-card__info2{
      .delivery-card__list li:before{
        background-image: url(../img/check4.svg);
      }
    }
  }
  &__top{
    height: 20.625em;
    padding: 1.25em 3em 1.875em;
    color: #fff;
    background-color: $secondary;
    position: relative;
  }
  &__info{
    width: calc(100% - 25em);
    min-height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__img{
    position: absolute;
    width: 23.75em;
    height: 21.375em;
    background-color: #fff;
    right: 1.875em;
    bottom: 1.875em;
    padding: 1.625em;
    box-shadow: 0 .1875em .3125em rgba(0, 0, 0, 0.25);
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__hint{
    margin-top: auto;
    padding-top: 1em;
  }
  &__bottom{
    padding: 2.5em 3.5em 9.25em;
    border: 1px solid $secondary;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__list{
    @include grid100gap(1.25em);
    li{
      display: flex;
      align-items: center;
      &:before{
        content: '';
        flex-shrink: 0;
        width: 1em;
        height: 1em;
        background-image: url(../img/check.svg);
        @include bgImgSize(100%);
        margin-right: 1.25em;
      }
    }
    span{
      font-size: 1.125em;
      line-height: 122%;
    }
  }
  &__info2{
    padding-top: 2em;
    margin-top: auto;
    .delivery-card__list{
      margin-top: 1.375em;
    }
    li{
      &:before{
        background-image: url(../img/check3.svg);
      }
    }
  }
  &__btn{
    width: 25em;
    left: unset;
    right: 0;
  }
  ._animate{
    transition: transform .5s, opacity .7s;
    opacity: 0;
    transform: translateY(50px);
  }
  ._animated{
    opacity: 1;
    transform: translateY(0);
  }
}

.custom{
  padding: 3.125em 0 3.5em;
  position: relative;
  &__items{
    display: flex;
    justify-content: space-between;
    margin-top: 4.125em;
  }
  &__line{
    width: 100%;
    max-width: 1920px;
    position: absolute;
    bottom: 12.0625em;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
  }
}

.custom-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: opacity .3s;
  opacity: 0;

  &._animated{
    opacity: 1;
  }
  &__img{
    padding-bottom: 1.5em;
    img{
      max-width: 13.875em;
      max-height: 11em;
    }
  }
  &__title{
    font-weight: 500;
    font-size: 1.375em;
    line-height: 123%;
    text-align: center;
  }
  @media screen and (min-width: 993px){
    &:nth-child(2){
      margin-top: -4.375em;
      transition-delay: .3s;
    }
    &:nth-child(3){
      margin-top: 3.125em;
      transition-delay: .6s;
    }
    &:nth-child(4){
      margin-top: -6.25em;
      transition-delay: .9s;
    }
    &:nth-child(5){
      margin-top: -.875em;
      transition-delay: 1.2s;
    }
  }
}



@media screen and (max-width: 992px){
  .main{
    &__inner{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__left{
      padding: 0;
    }
    &__text2{
      flex-direction: column;
      align-items: flex-start;
      margin: .25em 0 0;
    }
    &__hint{
      transform: unset;
      margin-top: .5em;
    }
    &__nav{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
    &__btn{
      height: 3.125em;
      width: 100%;
      flex-direction: row;
      align-items: center;
      padding: 0 1em;
      br{
        display: none;
      }
      &-bg{
        display: none;
      }
      &-icon{
        width: 1.5em;
      }
    }
    &__img{
      img{
        @include fullWidth;
      }
    }
  }

  .info-block{
    p{
      font-size: 1em;
    }
  }

  .advantages{
    padding: 1em 0;
    margin-top: 1em;
    &__grid{
      margin-top: 1em;
      grid-template-columns: 50% 50%;
    }
  }

  .advantage-item{
    padding: .5em 1em 1em;
    min-height: 10em;
  }

  .advantage-insurance{
    &__percent{
      &-title{
        margin: 0;
      }
    }
  }

  .advantage-office{
    &__num{
      align-items: center;
      &-title{
        padding: 0;
        margin-left: .5em;
      }
    }
  }

  .advantage-inspector{
    &__text{
      align-items: center;
      &-title{
        padding: 0 0 0 .5em;
      }
    }
  }

  .advantage-days{
    &__text{
      align-items: center;
      &-title{
        padding: 0 0 0 .5em;
      }
    }
  }

  .advantage-office,
  .advantage-overpayment{
    margin-left: 0;
  }

  .consult{
    padding: 2em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .consult-info{
    padding: 1em;
    &__bottom{
      margin-top: 3em;
    }
  }

  .consult-form,
  .prices{
    &__btn{
      margin-top: 1em;
      transform: unset;
      width: 100%;
      height: 3.125em;
      padding: 0 1em;
      align-items: center;
      img{
        width: 1.5em;
        height: auto;
        position: static;
      }
      span{
        text-decoration: none;
      }
    }
  }

  .consult-form{
    padding: 0;
  }

  .services{
    padding: 1em 0;
    &__top{
      display: block;
    }
    &__title{
      margin-top: 1em;
      margin-left: 0;
    }
    &__grid{
      margin-top: 1.55em;
    }
  }

  .service-card,
  .delivery-card{
    &__btn{
      opacity: 1;
      bottom: 0;
      height: 3.125em;
      padding: 0 1em;
      span{
        font-size: .875em;
      }
      img{
        width: 1.5em;
      }
    }
  }

  .service-card{
    &__content{
      padding: 1em 1em 4em;
    }
    &__icon{
      height: 2em;
    }
    &__title{
      margin-top: 1em;
      span{
        font-size: 1.5em;
      }
    }

  }

  .prices{
    padding: 1em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 2em;
    }
    &__left{
      padding: 0;
    }
    &__text{
      margin-top: 1em;
    }
    &__text2{
      padding: 0;
    }
    &__bottom{
      margin-top: 1em;
      display: block;
      padding: 0;
    }
    &__img2{
      display: none;
    }
    .services__title{
      margin-top: 1em;
      .text48{
        font-size: 1.375em;
      }
      .primary-text{
        margin-top: 0;
      }
    }
  }

  .delivery{
    padding: 1em 0;
    &__top{
      margin: 0;
      display: block;
    }
    &__title{
      padding: 0;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 3em;
    }
  }

  .delivery-card{
    &__top{
      padding: 1em;
      height: unset;
      display: flex;
      justify-content: space-between;
    }
    &__info{
      min-height: unset;
      width: unset;
      max-width: 20em;
      padding-right: 1em;
    }
    &__img{
      position: static;
      height: unset;
      width: 10em;
    }
    &__bottom{
      padding: 1em 1em 4em;
    }
    &__btn{
      width: 100%;
    }
    &__list{
      gap: .75em;
      li{
        &:before{
          width: .75em;
          height: .75em;
          margin-right: .5em;
        }
      }
      span{
        font-size: .875em;
      }
    }
    &__info{
      .delivery-card__list{
        margin-top: 1em;
      }
    }
  }

  .custom{
    padding: 1em 0;
    &__items{
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 1.5em;
      margin-top: 1.5em;
    }
    &__line{
      display: none;
    }
  }

  .custom-card{
    &__img{
      height: 11em;
      img{
        max-height: 100%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__nav{
      grid-template-columns: 100%;
    }
  }

  .advantages{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .advantage-item{
    grid-column: 1/-1;
    margin-left: 0;
    &:not(:first-child){
      margin-top: -1px;
    }
  }

  .services{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .service-card{
    &__img{
      padding-bottom: 150%;
    }
  }

  .delivery{
    &__grid{
      gap: 1.5em;
    }
  }

  .delivery-card{
    &__img{
      display: none;
    }
  }

  .custom{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .custom-card{
    &__img{
      height: 8em;
      padding-bottom: 1em;
    }
    &__title{
      font-size: 1em;
    }
  }
}
