.logo{
  display: block;
  margin-right: auto;
  width: 18.9375em;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 3.75em 0 2.5em;
  &__inner{
    display: flex;
    justify-content: flex-end;
    @media screen and (min-width: 993px){
      width: calc(100% + 6.875em);
      margin-left: -1.25em;
    }
  }
  &__logo{
    padding-top: .75em;
  }
  &__buttons{
    display: flex;
  }
  &__btn{
    flex-shrink: 0;
    width: 18em;
    height: 5.75em;
    flex-direction: column;
    align-items: unset;
    text-align: left;
    padding: 0 5em 0 2.125em;
    text-transform: uppercase;
    position: relative;
    i{
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 4.875em;
      @include center;
      border-left: 1px solid rgba(#fff,.3);
    }
    img{
      width: 1.875em;
      display: block;
    }
    span{
      font-size: 1em;
      line-height: 119%;
    }
    strong{
      font-weight: 500;
    }
  }
  &__phone{
    margin-left: 3em;
  }
}

.burger{
  display: none;
}

.menu{
  display: flex;
  align-items: center;
  align-self: center;
  margin-right: 3em;
  padding: 1.75em 0;
  border-bottom: 1px solid #C8C8C8;
}

.socials{
  display: flex;
  align-items: center;
  margin-left: 1.625em;
  &__item{
    width: 1.875em;
    @include lastM(0 .75em 0 0);
    &:hover{
      img{
        transform: translateY(-30%);
      }
    }
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
  }
}


@media screen and (max-width: 992px){
  .logo{
    width: 10em;
  }

  .header{
    padding: 1em 0;
    &__inner{
      align-items: center;
    }
    &__logo{
      padding: 0;
    }
    &__btn{
      padding: 0;
      width: 2.5em;
      height: 2.5em;
      align-items: center;
      span,
      strong{
        display: none;
      }
      i{
        position: static;
        border: none;
        height: unset;
        width: unset;
      }
      img{
        width: 1.25em;
      }
    }
    &._open{
      .burger{
        background-image: url(../img/burger2.svg);
        background-size: 1.25em auto;
      }
      .menu{
        transform: translateX(0%);
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .menu{
    display: none;
  }

  .burger{
    z-index: 11;
    position: relative;
    display: none;
    flex-shrink: 0;
    width: 2.5em;
    height: 2.5em;
    background-color: $secondary;
    margin-left: .5em;
    background-image: url(../img/burger.svg);
    @include bgImgSize(1.625em);
  }
}
