.map{
  height: 37.5em;
}

.contacts{
  min-height: 35em;
  width: 32.625em;
  background-color: $primary;
  position: absolute;
  bottom: 0;
  left: 0;
  color: #fff;
  a{
    &:hover{
      color: rgba(#fff,.7);
    }
  }
  &__top{
    padding: 2.5em 2.8125em 1em;
    border-bottom: 1px solid #FFFFFF;
  }
  &__items{
    padding: 2.25em 3.75em;
    @include grid100gap(3em);
  }
  &__item{
    display: flex;
    &._center{
      align-items: center;
    }
  }
  &__icon{
    width: 1.875em;
    height: 1.875em;
    @include center;
    margin-right: 1em;
    flex-shrink: 0;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__phone{
    font-size: 2.25em;
    font-weight: 500;
    line-height: 122%;
  }
  .socials{
    margin: 0;
    height: 7.25em;
    padding: 0 2.375em;
    background-color: $secondary;
    position: absolute;
    right: 0;
    bottom: 0;
    transform: translateX(50%);
  }
}

.footer{
  &__inner{
    position: relative;
    padding: 2em 0 2em 41em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__c,
  &__link{
    font-size: .75em;
    line-height: 125%;
    color: #9CA4B0;
    font-weight: 500;
  }
  &__by{
    display: inline-flex;
    align-items: center;
    margin-top: 1em;
    svg{
      width: 3em;
      height: auto;
      margin-right: 1em;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 992px){
  .map{
    height: 20em;
  }

  .contacts{
    width: 100%;
    min-height: unset;
    position: static;
    @include fullScroll;
    padding: 0;
    &__top{
      padding: 1em;
    }
    &__items{
      padding: 1em;
      gap: 1em;
    }
    &__icon{
      width: 1.5em;
      height: 1.5em;
    }
    &__phone{
      font-size: 1.5em;
    }
    .socials{
      margin-top: 1em;
      width: 100%;
      transform: unset;
      position: static;
      padding: 1em;
      height: unset;
      background-color: transparent;
    }
  }

  .footer{
    &__inner{
      padding: 0 0 1.5em;
      flex-direction: column;
    }
    &__c{
      margin-top: 2em;
    }
    &__link{
      margin-top: 1em;
    }
    &__by{
      margin-top: 1em;
      order: 3;
      svg{
        width: 2.5em;
      }
      span{
        font-size: .75em;
      }
    }
  }
}
