.text200{
  font-size: 12.5em;
  line-height: 124%;
  text-transform: uppercase;
  font-weight: 800;
}

.text128{
  font-size: 8em;
  line-height: 124%;
}

.text64{
  font-size: 4em;
  line-height: 123%;
}

.text48{
  font-size: 3em;
  line-height: 125%;
}

.text36{
  font-size: 2.25em;
  line-height: 122%;
}

.text32{
  font-size: 2em;
  line-height: 122%;
}

.text30{
  font-size: 1.875em;
  line-height: 123%;
}

.text24{
  font-size: 1.5em;
  line-height: 121%;
}

.text20{
  font-size: 1.25em;
  line-height: 125%;
}

.text18{
  font-size: 1.125em;
  line-height: 117%;
}

.text12{
  font-size: .75em;
  line-height: 125%;
}

.underline-text{
  text-decoration: underline;
}

.uppercase-text{
  text-transform: uppercase;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.medium-text{
  font-weight: 500;
}

.primary-text{
  color: $primary;
}

.secondary-text{
  color: $secondary;
}

.dark-text{
  color: $dark;
}



@media screen and (max-width: 992px){
  .text200{
    font-size: 3em;
  }

  .text128{
    font-size: 2.5em;
  }

  .text64,
  .text48{
    font-size: 1.75em;
  }

  .text36{
    font-size: 1.5em;
  }

  .text32,
  .text30{
    font-size: 1em;
  }

  .text24,
  .text20,
  .text18{
    font-size: .875em;
  }

}
